.detailPage-container {
    margin-bottom: 30rem;
    padding-top: 5rem !important;
}


.quantity-c button {
    background-color: transparent;
    border: 1px solid rgba(0, 0, 0, 0.404);
    padding: 0px 1rem;
}

.btn-c button{
    font-weight: bold;
    padding: .5rem 1rem;
    margin: 0px .5rem;
    transition: .15s;
    background-color: white;
}

.btn-c button:nth-child(1) {
    color: orangered;
    border: 1px solid orangered;
}


.btn-c button:nth-child(2) {
    color: white;
    background-color: orangered;
    border: 1px solid orangered;
}


.btn-c {
    padding: 2rem 0px;
}


.detail-img img {
   background-color: rgba(128, 128, 128, 0.123);
   border-radius: 15px;
}


.detail-img img:hover {
    background-color: rgba(255, 68, 0, 0.301);
    cursor: pointer;
}

.up:hover, .down:hover {
    background-color: rgba(128, 128, 128, 0.486);
}

.detail_page_left_part .big_img {
    /* border: 2px solid red; */
    width: 100% !important;
    display: flex;
    justify-content: center;
    align-items: center;
}

.detail_page_left_part .big_img  img {
    width: 25rem !important;
}



.detail_container {
    /* border: 2px solid green !important; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 !important;
    padding: 0 !important;
}

@media(max-width: 780px) {
    .detailPage-container {
        padding-top: 7rem !important;
        padding-bottom: 13rem !important;
    }

