.carte-c {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 150vh !important;
}

.cart-bar {
    background-color: rgba(255, 68, 0, 0.178);
}


.carte-c .up,
.carte-c .down {
    background-color: rgba(255, 68, 0, 0.178);
    border: 1px solid orangered;
    font-weight: bold;
}

.carte-c2 {
    width: 90%;
}

.carte-c .up:hover,
.carte-c .down:hover {
    background-color: rgba(255, 68, 0, 0.555);
}

.del {
    background-color: orangered;
    border: none;
    padding: .1rem 1rem;
    color: white;
    font-weight: 600;
}

h6 button {
    padding: .1rem 1rem;
    border: 1px solid gray;
    margin: .21rem;
}


.cart-item {
    background-color: rgba(128, 128, 128, 0.103);
}






.total-c {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    padding-top: 3rem;

}

.total-c2 {
    text-align: center;
}

.total-c3 {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

}



.total-c button {
    padding: .1rem 1rem;
    width: 100%;
}

.total-c .clear {
    border: 1px solid orangered;
    border-radius: 50px;
    color: orangered;
    font-size: 1.2rem;
    margin-bottom: 2rem;
    background-color: rgba(255, 68, 0, 0.082);
}

.total-c .submit {
    border: 1px solid orangered;
    background-color: orangered;
    color: white;
    font-size: 1.3rem;
    margin-top: 1.5rem;
}

@media(max-width: 501px) {
    .cart-item {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: .51rem;
    }

    .cart-item .left {
        width: 100%;
    }

    .cart-item .left img {
        width: 15rem !important;
    }

    .cart-item .right {
        width: 70%;
        min-width: 297px !important;
    }


}