body {
    position: relative;
}

.pupup-c {
    background-color: rgba(0, 0, 0, 0.548);
    display: flex;
    justify-content: center;
    position: fixed;
    inset: 0;
    z-index: 9999999999999999999999999;


    /* display: none; */
    
   
}

.pupup-containt {
    margin-top: 2em;
    min-width: 20rem;
    width: 50%;
    padding: 1rem;
    
}

.pupup-white {
    background-color: white;
    padding: 2rem 0px;
}

.pupup-c h3 {
    color: rgb(7, 168, 7);
}

.pupup-c h4 {
    font-family: 'Times New Roman', Times, serif;
}

.pupup-c h5 {
    color: rgb(0, 0, 0);
}

.pupup-c span {
    color: red;
}

.pupup-c button {
    padding: .2rem 1rem;
    margin: .5rem;
    border: none;
    font-weight: bold;
    width: 9rem;
}

.add {
    background-color: orangered;
    color: white;

}

.back {
    background-color: white;
    border: 1px solid orangered !important;
    color: orangered;
}





