
body {
    position: relative;
}

.product-cc {
    background-color: white;
    /* margin-bottom: 120rem !important; */
    margin-top: 6rem;
}

.Left-2 {
    position: absolute;
    inset: 0;
    top: 4rem;
}


.left-big-c {
    display: flex;
    justify-content: end;
    overflow-x: auto;
    /* height: 100vh; */
    background-color: white;
    border: 2px solid red;
    /* min-width: 13rem; */
    


    
}


.left-c {
    background-color: rgb(240, 239, 238);
    padding: 1rem 0px 0px 2rem;
    z-index: 11;

    top: 6rem;
    position: fixed;
    left: 0;
    bottom: 0;
    left: 0;
    min-width: 15rem;
    width: 25vw;



}

.close_btn {
    border: 1px solid orangered;
    border-radius: 4px;
    background-color: transparent;
    font-weight: 500;
    font-size: 1.2rem;
    /* transform: translate(30px ,-40px); */
    color: orangered !important;
    display: none;
}

.left-c label {
    margin-left: 1rem;
}

.catego h5 {
    margin-top: .5rem;
} 


@media (max-width: 768px) {
    .left-big-c  {
      border: 3px solid green !important;
        /* width: 100%; */
        display: flex;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.568);
        overflow-y: auto;


        position: fixed;
        /* width: 25%; */
        width: 100vw;
        top: 0rem;
        z-index: 1;
        padding-top: 7rem;
        background-color: rgb(240, 239, 238);  
        z-index: 999999999;

        /* transform: translateX(-100%); */
        display: none;

        
      }
      
      .left-big-c.active {
        /* transform: translateX(0%); */
        display: flex;


    }

    .left-c {
        width: 50%;
        min-width: 20rem;
        height: 130%;
        padding: 3rem;
        border: 1px solid  gray;

    }

    .close_btn {
        display: block;
    }

    .filter_container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }

}












.items-c {
  position: relative;
    display: grid !important;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    grid-template-columns: repeat(auto-fill, 10rem) !important;

}

.item-c {
    background-color: rgba(0, 0, 0, 0.082);
    padding: 0px .261rem;
    padding-top: .261rem;
    position: relative;
}


.rightt {
    padding-bottom: 40rem;
}




.add-to-cart {
    border: 1px solid orangered;
    background-color: transparent;
    color: orangered;
    font-weight: bold;
    padding: 0px 1rem;
    border-radius: 10px;
}

.add-to-cart:hover {
    background-color: orangered;
    color: white;
}


.test-btn {
    background-color: red !important;
    z-index: 9999999999999999999999999999999999999999999;
  }

  .Single_product_img_container {
    /* border: 3px solid red; */
    height: 11rem !important;
    width: 100% !important;
  }

  .Single_product_img_container img {
    width: 100% !important;
    height: 100% !important;
    object-fit: cover;
  }

  .desc {
    /* border: 1px solid red; */
    width: 100% !important;
    max-height: 3rem !important;
    overflow: hidden;
    text-overflow: ellipsis;
  }

 

  .nav_favorit_part {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .371rem;
    color: white !important;
  }

  .nav_favorit_part p {
     font-size: 1.5rem !important;
     margin: 0 !important;
     color: white;
  }

  .nav_favorit_part i {
    font-size: 1.5rem !important;
  }




  @media(max-width: 501px) {
  .hero_four_items .single_pro {
      width: 90vw !important;
  }   
  
  .items-c {
    gap: 1rem;
    grid-template-columns:1fr 1fr !important;
    padding: 0 1rem !important;

}
  }

  @media(max-width: 400px) {
   .special_pro_box {
    border: 2px solid rgb(255, 141, 141);
    display: flex;
    flex-direction: column;
  }   

  .special_pro_box .img,
  .special_pro_box .special {
    width: 100% !important;
    padding: 0 1rem !important;
  }

  .special_pro_box img {
    width: 80vw !important;
  }

  .off {
    width: 4rem !important;
  }


  }

  @media(max-width: 320px) {
    .items-c {
        grid-template-columns:1fr !important;
        padding: 0 1rem !important;
    
    }
  }

  .special_pro_box {
    border: 2px solid rgb(255, 194, 194);
  }  








