.favorit_big_container {
    padding-top: 10rem !important;

}


.favorit-container {
    display: flex !important;
    padding-bottom: 40rem !important;
    /* min-height: 120vh; */
}



.favorit-return {
    /* padding-top: 8rem; */
    grid-template-columns: repeat(auto-fill, 10rem) !important;
    display: grid;
    gap: 2rem;
    width: 100vw;
    justify-content: center;


}

.favorit-item {
    padding: .8rem .5rem ;
    background-color: rgba(128, 128, 128, 0.219);
    position: relative;
    /* border: 2px solid red; */
    
}

.favorit-item p {
    /* border: 1px solid red; */
    white-space: nowrap;
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
}

.empty-favorit {
    width: 100vw !important;
    margin-top: 10rem;
}

.favorit_img_box img{
    /* border: 1px solid red; */
    width: 100%;
    height: 8rem !important;
    object-fit: cover;
}

@media(max-width: 501px) {
    .favorit-container {
        /* padding-top: 3rem !important; */
    } 

    
.favorit-return {
    grid-template-columns: 1fr 1fr !important;
    gap: .5rem;



}
}

@media(max-width: 310px) {
    
.favorit-return {
    grid-template-columns: 1fr !important;
}
}

.favorit_title {
    text-align: center;
    padding: 1rem 0 !important;
}