/* //////////////////////////////////////// */
           /* Hero1 */
.heroo {
    position: relative;
    /* background-color: rgba(128, 128, 128, 0.37); */
    background-color: rgba(255, 68, 0, 0.521);
    background-color: rgba(189, 53, 4, 0.432);
    /* background-image: red  blue; */
    background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.397),
    rgba(0, 0, 0, 0)
  ), url(image/shooping2.png);
    z-index: -1;
    border-radius: 2rem;
    background-repeat: no-repeat;
    background-position: right, top ;
}

.img-hero {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    left: 30%;
    z-index: -1;
}

.maint_hero_img img {
   min-width: 20rem !important;
}

.img-hero img {
    height: 100% ;
    width: 100%;
    object-fit: contain;
}

.heroo h2 {
    font-size: 3rem;
    color: white !important;
}

.heroo p {
    font-size: 20px;
}

.hero_desc {
    width: 100% !important;
    font-weight: bold !important;
    color: rgb(253, 253, 253);
    text-shadow: .12rem .12rem .12rem rgb(12, 12, 12);
    /* background-color: rgba(255, 255, 255, 0.473); */
}

.heroo button, .cartes button {
    border: 1px solid orangered;
    color: orangered;
    background-color: transparent;
    /* background-color: rgb(206, 178, 23); */
    border-radius: 5px;
    padding: .235rem 1rem;
    font-weight: bold;
}

/* ///////////////////////////////////// */
.cartes {
    position: relative;
    border-radius: 1rem;
    z-index: -1;
    padding: 0 1rem !important;
    width: 100%;
    color: black !important;
    background-image: linear-gradient(to right, rgb(255, 255, 255), rgba(255, 255, 255, 0));
}



.hero_four_details {
    width: auto;
}

.cartes h5,
.cartes p {
    text-shadow: 0.12rem 0rem .1rem white;
    -webkit-text-stroke: .01rem rgb(255, 255, 255);
    color: black;
    font-weight: bold;
    width: auto !important;
    cursor: pointer !important;
    z-index: 9999999999999999999999999999999999999999999999999999999999999999999;
}

