 }

@media(max-width: 680px) {
    .header1{
        padding: 0 1rem !important;
    }
    .header1  button,
    .header1  input {
        display: none !important;
    }

    .header1.active  button,
    .header1.active  input {
        display: inline-block !important;
    }

    .header1 .input-c i{
       font-size: 1.4rem !important;
       display: block;
       
    }



    .header1.active .header_logo {
       display: none !important;
    }

    .header1.active .nav_end_part {
        display: none !important;
    }

    .header1.active .input-c i {
        display: none;
    }

    .header1.active {
        display: flex;
        flex-wrap: nowrap;
    }

    .header1.active .input-c {
        width: 100% !important;
    }
}

@media(max-width: 501px) {
    .header1 {
        padding: 0 1rem !important;
        margin: 0 !important;
        justify-content: space-between;
    }


    .maint_hero_img {
